  new Vue({
    el: '#app',
    data: model,
    computed:{
      filtered: function(){
        var pname = this.projectname.trim();

        // return this.items.filter(function(item,index){
        //   if (item.name.indexOf(pname) > -1){
        //     return true;
        //   }
          
        // })


        //input
        if (!this.checkedTypes.length) {
          //$('.pj_search').prop('disabled', false);
          return this.items.filter(item => {
            return item.name.indexOf(pname) > -1
          });
        }
        //checkbox
        else {
          //$('.pj_search').prop('disabled', true);
          return this.items.filter(item => {
              return this.checkedTypes.includes(item.type);
          });
        }
      
      }
    },
    mounted(){
        //전체 목록 불러옴 원래 체크된것 해제
        $('.pj_search').on('focus', function(){
          $('.fav_search').find('input:checked').trigger('click');
        });

        var allItem = this.items;
        var allLeng = this.items.length;
        

        // console.log(allItem,allLeng);

        var leng01 = $("li.date:contains('18.01')").length;
        // $('.leng01').text(leng01);

        $('.form_group span').each(function(){
          var _className = $(this).attr('class');
          var _lengs = $('li.type:contains('+_className+')').length;
          $(this).text(_lengs);
        });

        $('.all').text(allLeng);

        


        // $('#chkAll').on('click', function(){
        //     $('.fav_search').find('input:checkbox').trigger('click');
        // });



        
    }


  });

